#controls-container {
  position: absolute;
  top: 8px;
  left: 50%;
  -webkit-transform: translateX(-50%);
      -ms-transform: translateX(-50%);
          transform: translateX(-50%);
  z-index: 90; /* less than menu */
}

#controls {
  font-size: 24px;
}

.control {
  cursor: pointer;
  margin: 0 10px;
}

#speed {
  text-align: center;
  font-size: 16px;
}

@media screen and (max-width: 600px) {
  #controls-container {
    left: 300px;
    width: 130px;
  }
}


